import Calendar from 'react-calendar';
import '../../styles/MyCalendar.scss';
import moment from 'moment';
import { useEffect, useState } from 'react';

export function MyCalendar() {
  const date = new Date();
  return (
    <div className="my-calendar-wrap">
      <Calendar
        value={date}
        calendarType="gregory"
        prevLabel={null}
        prev2Label={null}
        nextLabel={null}
        next2Label={null}
        showNeighboringMonth={true}
        minDetail="month" //   ''   
        maxDetail="month"
        formatDay={(locale, date) => moment(date).format('DD')}
      />
    </div>
  );
}

export function CurrentTime() {
  const date = new Date();
  const year = date.getFullYear();
  const month = date.getMonth() + 1; //  0  +1
  const day = date.getDate();
  const dayOfWeek = date.getDay();
  const hours = format(date.getHours());
  const minutes = format(date.getMinutes());
  const seconds = format(date.getSeconds());

  const days = ['', '', '', '', '', '', ''];
  const koreanDayOfWeek = days[dayOfWeek];

  const formatDate = `${hours}:${minutes}:${seconds}`;
  const [runningTime, setRunningTime] = useState(null);

  useEffect(() => {
    const interval = setInterval(() => {
      setRunningTime(formatDate);
    }, 1000);

    return () => {
      clearInterval(interval);
    };
  }, [formatDate]);
  return (
    <p className="my-calendar-date">
      <span>
        {year} {month} {day} {koreanDayOfWeek}
      </span>
      <span className="bg">{runningTime}</span>
    </p>
  );
}

function format(num) {
  return num < 10 ? '0' + num : num.toString();
}
